<template>
  <section class="carousel">
    <div class="carousel--main">
      <div class="carousel--bg">
        <template v-for="(post, index) in posts">
          <img :key="post.id" :src="`https://www.its-silver.com${post.thumb}`" alt="" :class="index === currentPost ? 'show' : 'hidden'" />
        </template>
      </div>

      <div class="carousel--desc" :class="{ 'carousel--desc_hidden': currentPost !== index }" v-for="(post, index) in posts" :key="post.id">
        <div class="wrapper" :class="carouselStatus(index)">
          <h1 class="title">{{ post.title || '' }}</h1>
          <p class="desc">{{ post.cate || '' }}</p>
          <BaseButton :link="post.href" iconclass="fal fa-chevron-right" color="#e94e77" background="white" style="border: 1px dashed #e94e77;">Read More</BaseButton>
        </div>
      </div>
    </div>
    <div class="carousel--aside">
      <div class="carousel--item" v-for="(post, index) in posts" :key="post.id">
        <div class="wrapper" :class="currentPost === index ? 'hover' : ''" @mouseenter="mouseEnter(post)" @mouseleave="mouseLeave">
          <time :datetime="post.date">{{ getDate(post) }}</time>
          <a class="title" :href="post.href">{{ post.title }}</a>
          <a class="desc" :href="post.cateHref">{{ post.cate }}</a>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { clearTimeout, setTimeout, setInterval, clearInterval } from 'timers'
export default {
  props: {
    posts: {
      type: Array,
      default: function() {
        return [
          {
            title: '',
            cate: '',
            href: '',
            cateHref: ''
          }
        ]
      }
    }
  },
  data() {
    return {
      currentPost: 0,
      /* 鼠标移入时间 */
      timer: null,
      delay: 100,
      /* 记录desc状态 */
      loopDelay: 5000,
      /* 记录轮播setInterval */
      intervalTimer: null
    }
  },
  mounted() {
    this.loop()
  },
  methods: {
    getDate(post) {
      const date = post.date || new Date()
      return `${date.getFullYear()}-${date.getMonth()}-${date.getDate()}`
    },
    /* mouseenter */
    mouseEnter(post) {
      this.timer = setTimeout(() => {
        this.currentPost = post.id
      }, this.delay)
      /* 清除掉interval */
      clearInterval(this.intervalTimer)
    },
    /* mouseleave */
    mouseLeave() {
      this.loop()
      clearTimeout(this.timer)
    },
    carouselStatus(index) {
      return this.currentPost === index ? 'carousel--wrap_show' : 'carousel--wrap_hidden'
    },
    /* 图片轮播 */
    loop() {
      this.intervalTimer = setInterval(() => {
        this.increaseIndex()
      }, this.loopDelay)
    },
    increaseIndex() {
      this.currentPost = (this.currentPost + 1) % this.posts.length
    },
    decreaseIndex() {
      this.currentPost = this.currentPost - 1 < 0 ? this.posts.length : this.currentPost
      this.currentPost -= 1
    }
  }
}
</script>

<style lang="scss" scoped>
$max-line-number: 3;
$small-text-color: #e8ddea;
$text-color: #e3f6ff;
$text-underline-color: #ff7434;
$dash-border-color: #569ba0;
$text-hover-color: #ff9c40;
$border-hover-color: #ffefc4;
$desc-transition: 0.5s ease-in;
$image-border-radius: 10px;
$aside-border-radius: 5px;
$padding-bottom: 5.5em; /* 用于设置clip-path 遮挡住的部分 */
@mixin shadow {
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.3), 1px 1px 20px rgba(0, 0, 0, 0.1);
}

.carousel {
  margin: auto;
  width: 100%;
  height: 580px;
  display: flex;
  .show {
    display: block;
  }
  .hidden {
    display: none;
  }
  .carousel--main {
    margin: 80px;
    @include shadow();
    border-radius: $image-border-radius;
    flex: 3.5 2;
    position: relative;
    font-size: 12px;
    h1,
    p {
      margin: 1em 0;
    }
    h1 {
      font-size: 1.5em;
      font-weight: bolder;
      font-style: italic;
      border-bottom: 1px dashed $text-underline-color;
    }
    p {
      text-transform: uppercase;
    }
    .carousel--bg {
      border-radius: $image-border-radius;
      border: 5px solid $dash-border-color;
      overflow: hidden;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      img {
        width: 100%;
        height: 100%;

        object-fit: cover;
      }
    }
    .carousel--desc {
      color: white;
      position: absolute;
      top: 50%;
      left: 0;
      right: 0;
      // transform: translateY(-50%);
      padding: 1em 15em 2em 1.5em;
      padding-bottom: $padding-bottom;
      background: rgba(0, 0, 0, 0.8);
      overflow: hidden;
      max-height: 210px;
      transition: 0.3s ease-in;
      opacity: 1;
      clip-path: polygon(0% 0%, 100% 0%, 100% 75%, 75% 75%, 75% 100%, 65% 75%, 0% 75%);
    }
    .carousel--desc_hidden {
      opacity: 0;
      pointer-events: none;
    }
    .carousel--wrap_show {
      opacity: 1;
      transform: translate(0);
      transition: 0.3s ease-in;
    }
    .carousel--wrap_hidden {
      pointer-events: none;
      transition: $desc-transition;
      opacity: 0;
      transform: translateX(20px);
    }
  }
  .carousel--aside {
    @include shadow;
    background: linear-gradient(to right, #0f2027, #203a43, #2c5364);
    // background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
    flex: 1 0;
    padding-top: 1em;
    flex-direction: column;
    box-sizing: border-box;
    display: flex;
    border-radius: $aside-border-radius;
    // border: 1px dashed $dash-border-color;
    time {
      font-size: 12px;
      color: $small-text-color;
    }
    a {
      cursor: pointer;
      display: block;
      &.title {
        font-size: 14px;
        margin: 0.4em 0;
        color: $text-color;
        text-decoration: none;
        display: -webkit-box;
        overflow: hidden;
        text-overflow: ellipsis;
        -webkit-line-clamp: $max-line-number;
        -webkit-box-orient: vertical;
        &:hover {
          color: $text-hover-color;
          text-decoration: $text-underline-color underline;
        }
      }
      &.desc {
        font-size: 12px;
        font-weight: bold;
        color: $small-text-color;
        &:hover {
          color: $text-hover-color;
          text-decoration: $text-underline-color underline;
        }
      }
    }

    .carousel--item {
      flex: 1;
      display: flex;
      overflow: hidden;
      .wrapper {
        flex: 1 1;
        overflow: hidden;
        margin: 0 1em 1em;
        padding: 0em 0.5em;
        transition: 0.1s ease-in;
        border: 1px solid transparent;
      }
      .hover {
        cursor: pointer;
        border: 1px solid $border-hover-color !important;
        box-shadow: 0px 0px 4px $border-hover-color;
      }
      &:not(:first-child) .wrapper {
        border-top: 1px dashed $dash-border-color;
      }
      &:first-child .wrapper {
        border-top: 1px dashed $dash-border-color;
      }
    }
  }
}
</style>
